import { FOOTER_MOBILE_EXTEND, FOOTER_PREFIX_CLS } from '@ezuikit/theme-plugin-footer';
import { HEADER_PREFIX_CLS } from '@ezuikit/theme-plugin-header';
import { PREFIX_CLASS_NAME, addClass, style } from '@ezuikit/utils-dom';

/**
 * @description 渲染播放窗口
 * @param $container
 * @param options
 */
export function _renderWindow($container: HTMLElement, options: any) {
  addClass($container, `${PREFIX_CLASS_NAME}-rec-theme`);
  style($container, {
    display: 'flex',
    flexDirection: 'column',
    position: 'relative',
    justifyContent: 'space-between',
    height: options.height + 'px',
    width: options.width + 'px'
  });
}

/**
 * @description 销毁窗口
 * @param $container
 */
export function _destroyWindow($container: HTMLElement) {
  $container.style.cssText = 'position: relative;';

  // 播放区域的dom id
  const $simplePlayer = $container.querySelector(`#${getPlayerId($container)}`);
  if ($simplePlayer) $container.removeChild($simplePlayer);

  const $header = $container.querySelector(`.${HEADER_PREFIX_CLS}`);
  if ($header) {
    $container.removeChild($header);
  }
  const $footer = $container.querySelector(`.${FOOTER_PREFIX_CLS}`);
  if ($footer) {
    $container.removeChild($footer);
  }

  const $footerMobile = $container.querySelector(`.${FOOTER_MOBILE_EXTEND}`);
  if ($footerMobile) {
    $container.removeChild($footerMobile);
  }
}

/**
 * @description 播放区域的dom id
 * @param $container
 * @returns {string}
 */
export function getPlayerId($container: HTMLElement) {
  const id = $container.getAttribute('id');
  if (id) {
    return `${id}-container`;
  }
  throw new Error('Theme Container dom id is empty!');
}
