import { DEFAULT_CONTROLS } from '../constant';
import { type IControls, type ControlsParam } from '../interface';

/**
 * @description 序列化插件, 进行分组
 * @param {Map<string, any>} plugins
 * @returns {Record<'header' | 'footer', any[]>}
 */
export function serializeControls(
  plugins: Map<string, any>,
  controls: IControls
): Record<'header' | 'footer' | 'bottom', any[]> {
  const result: Record<'header' | 'footer' | 'bottom', any[]> = {
    header: [],
    footer: [],
    bottom: []
  };

  const controlsList = controls
    .map((item) => {
      let control = null;
      if (typeof item === 'string') {
        control = DEFAULT_CONTROLS.find((control) => control.name === item);
        return control;
      } else {
        control = DEFAULT_CONTROLS.find((control) => control.name === item.name);
        if (control) {
          return {
            ...control,
            ...item
          };
        }
      }
      return null;
    })
    .filter((item) => !!item) as ControlsParam[];

  controlsList.forEach((cur) => {
    const plugin = plugins.get(cur.name);
    if (plugin) {
      if (cur?.position.startsWith('h')) {
        result.header.push({
          name: cur.name,
          plugin,
          position: cur.position
        });
      } else if (cur?.position.startsWith('f')) {
        result.footer.push({
          name: cur.name,
          plugin,
          position: cur.position
        });
      } else if (cur?.position.startsWith('b')) {
        result.bottom.push({
          name: cur.name,
          plugin,
          position: cur.position
        });
      }
    }
  });

  return result;
}
