import { style } from '@ezuikit/utils-dom';
import type RecTheme from '../main';
import { HEADER_PREFIX_CLS } from '@ezuikit/theme-plugin-header';
import { FOOTER_TOP_CLS } from '@ezuikit/theme-plugin-footer';

/**
 * 渲染皮肤
 */
export function _renderThemeSkin(theme: RecTheme, data: any) {
  const { headerStyle, footerStyle } = data;
  if (headerStyle) {
    const $header = theme.$container.querySelector(`.${HEADER_PREFIX_CLS}`) as HTMLElement;
    if ($header) {
      style($header, {
        background: headerStyle.backgroundColor,
        color: headerStyle.color
      });
      theme.$container.style.setProperty(
        '--ezui-header-background-color',
        headerStyle.backgroundColor
      );
      theme.$container.style.setProperty('--ezui-header-color', headerStyle.color);
      theme.$container.style.setProperty('--ezui-header-active-color', headerStyle.backgroundColor);
    }
  }
  if (footerStyle) {
    const $footer = theme.$container.querySelector(`.${FOOTER_TOP_CLS}`) as HTMLElement;
    if ($footer) {
      style($footer, {
        background: footerStyle.backgroundColor,
        color: footerStyle.color
      });
      theme.$container.style.setProperty(
        '--ezui-footer-background-color',
        footerStyle.backgroundColor
      );
      theme.$container.style.setProperty('--ezui-footer-color', footerStyle.color);
      theme.$container.style.setProperty('--ezui-active-color', footerStyle.activeColor);
    }
  }

  if (data.poster) {
    theme.setPoster(data.poster);
  }

  if (data.autoFocus >= 0) {
    theme.event?.emit('footer-autoFocus', data.autoFocus);
  }
}
