export type ClickCallBack = (ele?: HTMLElement) => void;

/**
 *
 * @param $container
 * @param classSelector class 选择器
 * @param callback
 */
export function delegationClick(
  $container: HTMLElement,
  classSelector: string,
  callback: ClickCallBack
) {
  // prettier-ignore
  $container.addEventListener('click',
    (e) => {
      let target = (e.target || e.srcElement) as HTMLElement;
      // console.log(target.tagName, classSelector);
      do {
        if (target === $container) break;
        if (target.classList.contains(classSelector.replace(/^\./, ''))) {
          callback(target);
          break;
        }

        if (!target.parentNode) break;

        target = target.parentNode as HTMLElement;
      } while (true);
    },
    false
  );
}
