import { type ThemeDetailRes } from '@ezuikit/utils-service';

import { DEFAULT_CONTROLS } from '../constant';

export function getThemeDetailById(player: any, id: string) {
  return player.service.getThemeDetailById(id).then((res: { data: ThemeDetailRes }) => {
    return serializeThemeData(res.data);
  });
}

function serializeThemeData(data: ThemeDetailRes) {
  const footerList = (data.footer?.btnList || []).reduce((acc: any[], cur: any) => {
    if (cur.isrender !== 1) {
      return acc;
    }

    const findItem = DEFAULT_CONTROLS.find(
      (item) => item.oldName === cur.iconId || item.name === cur.iconId
    );
    if (findItem) {
      acc.push({
        ...findItem,
        position: cur.part === 'left' ? 'fl' : 'fr'
      });
    }
    return acc;
  }, []);

  const headerList = (data.header?.btnList || []).reduce((acc: any[], cur: any) => {
    if (cur.isrender !== 1) {
      return acc;
    }

    const findItem = DEFAULT_CONTROLS.find(
      (item) => item.oldName === cur.iconId || item.name === cur.iconId
    );
    if (findItem) {
      delete findItem.oldName;
      acc.push({
        ...findItem,
        position: cur.part === 'left' ? 'hl' : 'hr'
      });
    }
    return acc;
  }, []);

  headerList.push({
    name: 'recTypes',
    position: 'hr'
  });

  const bottomList = [
    {
      name: 'timeline',
      position: 'bl'
    },
    {
      name: 'datepicker',
      position: 'br'
    }
  ];

  const headerStyle = data.header;
  const footerStyle = data.footer;

  return {
    list: [...headerList, ...footerList, ...bottomList],
    poster: data.poster,
    autoFocus: data.autoFocus,
    headerStyle,
    footerStyle
  };
}
