import './styles/index.scss';
import SimpleTheme, {
  type ISimpleThemeOptions,
  type EZopenPlayerOptions
} from '@ezuikit/theme-simple';
import Logger from '@ezuikit/utils-logger';

import HeaderPlugin, { HEADER_PREFIX_CLS } from '@ezuikit/theme-plugin-header';
import FooterPlugin, {
  FOOTER_PREFIX_CLS,
  FOOTER_MOBILE_EXTEND
} from '@ezuikit/theme-plugin-footer';

import { addClass, removeClass, PREFIX_CLASS_NAME } from '@ezuikit/utils-dom';
import { merge } from 'lodash-es';
import FullScreen from '@ezuikit/theme-plugin-fullscreen';
import WebFullScreen from '@ezuikit/theme-plugin-webfullscreen';
import SnapshotPlugin from '@ezuikit/theme-plugin-snapshot';
import { DeviceIDPlugin, DeviceNamePlugin } from '@ezuikit/theme-plugin-deviceinfo';
import ZoomPlugin from '@ezuikit/theme-plugin-zoom';
import RecordPlugin from '@ezuikit/theme-plugin-record';
import VolumePlugin from '@ezuikit/theme-plugin-volume';
import PlaybackRatePlugin from '@ezuikit/theme-plugin-playback-rate';
import RecTypesPlugin from '@ezuikit/theme-plugin-rec-types';
import PlayPlugin from '@ezuikit/theme-plugin-play';

import { type ControlsParam, type RecThemeOptions } from './interface';
import { DEFAULT_OPTIONS, MobileHideControls } from './constant';
import { serializeControls } from './utils/plugins';
import TimeLinePlugin from './components/TimeLine';
import DatePickerPlugin from './components/DatePicker';
import { getThemeDetailById } from './api/_http';
import { cancelMobileFullScreen, requestMobileFullScreen } from './utils/mobileFullScreen';
import { _renderThemeSkin } from './api/_theme';
import { _destroyWindow, _renderWindow } from './api/_window';

class RecTheme extends SimpleTheme {
  static version: '__VERSION__';
  options: RecThemeOptions;
  static $container: HTMLElement;
  private readonly _plugins: Record<string, any> = {};

  mobileFullScreen: boolean = false;

  constructor(options: Partial<RecThemeOptions>) {
    super(RecTheme._mergeOptions(options));
    if (options.isMobile) {
      options.footerHeight = 0;
    }

    this.options = Object.assign({}, DEFAULT_OPTIONS, options) as RecThemeOptions;

    this.logger = Logger({
      level: this.options?.logger?.level ?? 'INFO',
      showTime: this.options?.logger?.showTime !== false,
      name: 'REC'
    });

    this.logger.log(this.options);

    if (this.urlInfo.type !== 'rec') {
      this.logger.error(`Play url is ${this.options.url}, Non playback address!`);
    }

    this.$container = document.getElementById(this.options?.id) as HTMLElement;

    _renderWindow(this.$container, options);

    if (options.template) {
      this._renderHeaderFooter([], true);
      getThemeDetailById(this, options.template)
        .then((res: any) => {
          this._renderThemeData(res.list);
          this._renderThemeSkin(res);
        })
        .catch((e: any) => {
          this.logger.error(e);
          this._renderPlugins();
        });
    } else {
      this._renderPlugins();
    }
  }

  /**
   * @description 播放
   * @param options
   * @returns
   */
  play(options?: Partial<Pick<EZopenPlayerOptions, 'url' | 'accessToken'>>) {
    if (options) {
      this.options = Object.assign(this.options, options || {});
    }
    return super.play(options);
  }

  /**
   * 渲染皮肤
   */
  private _renderThemeSkin(data: any) {
    _renderThemeSkin(this, data);
  }

  /**
   * @description 设置和并输入的配置项
   * @param {Partial<RecThemeOptions>} options
   * @returns {Object}
   */
  private static _mergeOptions(options: Partial<RecThemeOptions>) {
    if (typeof options.id !== 'string') {
      throw new Error('id is required!');
    }

    options = merge({}, DEFAULT_OPTIONS, options) as RecThemeOptions;

    // ----- 极简主题容器创建 ------
    const simpleContainerId = `${options?.id}-container`;
    this.$container = document.getElementById(options?.id as string) as HTMLElement;
    const $playerContainer = document.createElement('div');
    $playerContainer.setAttribute('id', simpleContainerId);
    addClass(
      this.$container,
      `${PREFIX_CLASS_NAME}-rec-theme`,
      options.isMobile ? `${PREFIX_CLASS_NAME}-theme-mobile` : ''
    );
    // style($playerContainer, {
    //   flex: '1 1 auto'
    // });
    if (!this.$container) {
      throw new Error(`The id ${options.id} dom node does not exist!`);
    }
    this.$container?.appendChild($playerContainer);

    return merge({}, options as ISimpleThemeOptions, {
      id: simpleContainerId,
      fullScreenEle: document.getElementById(options?.id as string),
      height:
        (options?.height as number) -
        (options.isMobile ? 32 : options?.headerHeight || 0) -
        (options.isMobile ? 0 : options?.footerHeight || 0)
    });
  }

  private _renderThemeData(list: ControlsParam[]) {
    const controls = serializeControls(RecTheme.plugins, list);
    this._pluginsCall();
    this._renderControls(controls);
    this._registerEvents();
  }

  private _renderHeaderFooter(controls: any, show?: boolean) {
    if (show || controls.header.length > 0) {
      HeaderPlugin().bind(this, this)();
    }
    if (show || controls.footer.length > 0 || controls.bottom.length > 0) {
      FooterPlugin().bind(this, this)();
    }
  }

  private _renderPlugins() {
    const controls = serializeControls(RecTheme.plugins, this.options.controls);
    this._renderHeaderFooter(controls);
    this._pluginsCall();
    this._renderControls(controls);
    this._registerEvents();
  }

  /**
   * @description 根据_plugins渲染插件
   */
  private _renderControls(controls: any) {
    if (controls.header.length > 0) {
      this._renderControl(controls.header);
    }
    //
    if (controls.footer.length > 0) {
      this._renderControl(controls.footer);
    }
    if (controls.bottom.length > 0) {
      this._renderControl(controls.bottom);
    }
  }

  /**
   * @description 渲染控件
   * @param {ControlsParam} controls
   */
  private _renderControl(controls: ControlsParam[]) {
    for (const control of controls) {
      const name = control.name;
      // TODO: 移动端不支持录制
      if (this.options.isMobile && MobileHideControls.includes(name)) continue;
      if (this._plugins[name]) this._plugins[name](control);
    }
  }

  /**
   * @description 给插件传参和改变插件this指向
   */
  private _pluginsCall() {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this;
    if (RecTheme.plugins) {
      RecTheme.plugins.forEach((_, name: string) => {
        // 移动端H5忽略录制和网页全屏（理论上H5应该使用网页全屏）
        if (!(this.options.isMobile && MobileHideControls.includes(name))) {
          const plugin = RecTheme.plugins.get(name);
          if (!plugin || typeof plugin !== 'function') {
            console.warn('plugin', name, 'is invalid');
          } else {
            // 改变插件的this 指向， this === RecTheme
            _this._plugins[name] = plugin.bind(_this, _this);
          }
        }
      });
    }
  }

  private _registerEvents() {
    this.event.on('exitScreen', () => {
      const $header = this.$container.getElementsByClassName(HEADER_PREFIX_CLS)[0] as HTMLElement;
      if ($header) {
        removeClass($header, `${PREFIX_CLASS_NAME}-hidden`);
      }
      const $footer = this.$container.getElementsByClassName(FOOTER_PREFIX_CLS)[0] as HTMLElement;
      if ($footer) {
        removeClass($footer, `${PREFIX_CLASS_NAME}-hidden`);
      }

      // prettier-ignore
      const $footerMobileExtend = this.$container.getElementsByClassName(FOOTER_MOBILE_EXTEND)[0] as HTMLElement;
      if ($footerMobileExtend) {
        removeClass($footerMobileExtend, `${PREFIX_CLASS_NAME}-hidden`);
      }
    });
  }

  /**
   * @description 全屏
   * @returns {Promise<void>}
   */
  fullScreen() {
    if (this.options.isMobile) {
      void requestMobileFullScreen(this.$container);
      try {
        void super.resize(
          document.documentElement.clientHeight,
          document.documentElement.clientWidth - 32
        );
      } catch (error) {}
      this.mobileFullScreen = true;
      return Promise.resolve();
    } else {
      const $header = this.$container.getElementsByClassName(HEADER_PREFIX_CLS)[0] as HTMLElement;
      if ($header) {
        addClass($header, `${PREFIX_CLASS_NAME}-hidden`);
      }
      const $footer = this.$container.getElementsByClassName(FOOTER_PREFIX_CLS)[0] as HTMLElement;
      if ($footer) {
        addClass($footer, `${PREFIX_CLASS_NAME}-hidden`);
      }

      // 当页面已经是全局状态 不再需要再次全屏（Safari 浏览器多次调用requestFullscreen 不生效）
      if (
        !!(
          document.fullscreen ||
          (document as any).mozFullScreen ||
          (document as any).webkitIsFullScreen ||
          (document as any).webkitFullScreen ||
          (document as any).msFullScreen
        )
      ) {
        return new Promise<void>((resolve) => {
          void super.resize(window.screen.width, window.screen.height).then(() => resolve());
        });
      }

      return super.fullScreen(window.screen.width, window.screen.height);
    }
  }

  /**
   * @description web全屏
   * @returns
   */
  webFullScreen() {
    return super.fullScreen(
      window.screen.width,
      window.screen.height - (this.options.footerHeight || 0) - (this.options.headerHeight || 0)
    );
  }

  /**
   * @description 退出全屏
   * @returns
   */
  exitScreen() {
    if (this.options.isMobile && this.mobileFullScreen) {
      this.mobileFullScreen = false;
      try {
        void this.resize(this.options?.width || 0, this.options?.height || 0);
      } catch (error) {}
      void cancelMobileFullScreen(
        this.$container,
        this.options?.width || 0,
        this.options?.height || 0
      ).then(() => {});
      return Promise.resolve();
    }
    return super.exitScreen();
  }

  /**
   * @description 设置主题尺寸， resize的值会被记录
   * @param width 宽
   * @param height 高
   * @returns {Promise}
   */
  resize(width: number, height: number) {
    let headerHeight = 0;
    let footerHeight = 0;
    const $header = this.$container.getElementsByClassName(HEADER_PREFIX_CLS)[0] as HTMLElement;
    if ($header) {
      headerHeight = $header.clientHeight;
    }
    const $footer = this.$container.getElementsByClassName(FOOTER_PREFIX_CLS)[0] as HTMLElement;
    if ($footer) {
      footerHeight = $footer.clientHeight;
    }

    return super.resize(width, height - footerHeight - headerHeight).then(() => {
      this.$container.style.width = width + 'px';
      this.$container.style.height = height + 'px';
      return { width, height };
    });
  }

  /**
   * @description 获取配置
   * @returns {object}
   */
  getOptions() {
    return this.options;
  }

  /**
   * @description 销毁
   */
  destroy() {
    _destroyWindow(this.$container);
    return super.destroy();
  }

  /**
   * @description 获取版本号
   * @returns {string}
   */
  getVersion() {
    return RecTheme.version;
  }
}

// 默认插件
/**
 * header 和 footer 必须在前面
 */
RecTheme.installs([
  {
    name: 'play',
    plugin: PlayPlugin()
  },
  {
    name: 'snapshot',
    plugin: SnapshotPlugin()
  },
  {
    name: 'volume',
    plugin: VolumePlugin()
  },
  {
    name: 'record',
    plugin: RecordPlugin()
  },
  {
    name: 'zoom',
    plugin: ZoomPlugin()
  },
  {
    name: 'playbackRate',
    plugin: PlaybackRatePlugin()
  },
  {
    name: 'webFullscreen',
    plugin: WebFullScreen()
  },
  {
    name: 'fullscreen',
    plugin: FullScreen()
  },
  {
    name: 'timeline',
    plugin: TimeLinePlugin()
  },
  {
    name: 'datepicker',
    plugin: DatePickerPlugin()
  },
  {
    name: 'deviceID',
    plugin: DeviceIDPlugin()
  },
  {
    name: 'deviceName',
    plugin: DeviceNamePlugin()
  },
  {
    name: 'recTypes',
    plugin: RecTypesPlugin()
  }
]);

export { type RecThemeOptions };
export default RecTheme;
