import './index.scss';
import { calendarIcon } from '@ezuikit/icons';
import { FOOTER_BOTTOM_RIGHT_CLS, FOOTER_MOBILE_EXTEND_DATE } from '@ezuikit/theme-plugin-footer';
import DatePickerLib from '@ezuikit/datepicker';
import { removeClass, PREFIX_CLASS_NAME, addClass, style } from '@ezuikit/utils-dom';
import { switchEzopenUrl } from '@ezuikit/utils-tools';
import dayjs, { type Dayjs } from 'dayjs';

export interface Props {
  /** 日历图标 */
  icon?: string;
  current?: Date;
  showTime?: boolean;
}

function DatePicker(props?: Props) {
  return function (theme: any) {
    const _this = theme;

    const $span = calendarIcon(props?.icon);

    addClass($span, `${PREFIX_CLASS_NAME}-calendar`);
    style($span, { color: '#FFF' });

    let begin = dayjs(_this.urlInfo.searchParams.begin + '');
    if (!begin.isValid()) begin = dayjs(dayjs().format('YYYY-MM-DD 00:00:00'));

    if (_this.options.isMobile) {
      // prettier-ignore
      const $extendDate = (_this.$container as HTMLElement).querySelector(`.${FOOTER_MOBILE_EXTEND_DATE}`);

      if ($extendDate) {
        $extendDate.innerHTML = `<div class="${PREFIX_CLASS_NAME}-mobile-extend-date-title">今日录像</div>
          <div class="${PREFIX_CLASS_NAME}-mobile-extend-date-container">
            <div>
                <label for="date">
                  <img src='https://resource.eziot.com/group2/M00/00/6A/CtwQF2F6VieAQrU9AAABP-_Nsqo949.png' />
                </label>
                <input type="date" name="date" value="${begin.format('YYYY-MM-DD')}"  />
              </div>
          </div>`;

        const $date = $extendDate.querySelector(`input[type="date"]`) as HTMLInputElement;
        if ($date) {
          $date?.addEventListener('change', (e: any) => {
            const value = e.target.value || dayjs().format('YYYY-MM-DD');
            console.log('日期变化', value);
            datePickerChange(value as string);
            $date.value = value;
          });
        }
      }
    } else {
      const dataPicker = new DatePickerLib({
        getPopupContainer: () => $span as HTMLElement,
        current: begin,
        showTime: true,
        showToday: true,
        // onOk 不适用ToDay
        // onOk: (date: Dayjs) => {
        //   // console.log('onOk', date?.format('YYYY-MM-DD HH:mm:ss'), _this.options.url);
        //   // datePickerChange(date);
        // },
        onChange: (date: Dayjs) => {
          console.log('onOk', date?.format('YYYY-MM-DD HH:mm:ss'), _this.options.url);
          datePickerChange(date);
        }
        // 还有bug
        // disabledDate(date: Dayjs) {
        //   return date > dayjs();
        // }
      });
      // prettier-ignore
      const $footerBottomRight = (_this.$container as HTMLElement).querySelector(`.${FOOTER_BOTTOM_RIGHT_CLS}`);
      if ($footerBottomRight) {
        $footerBottomRight.appendChild($span);
      }

      $span.addEventListener('click', () => {
        if ($span.classList.contains(`${PREFIX_CLASS_NAME}-disable`)) {
          return;
        }
      });

      _this.event.on('resize', () => {
        dataPicker.hide();
      });

      _this.event.on('urlChange', () => {
        dataPicker.hide();
      });
    }

    function datePickerChange(date: string | Dayjs) {
      _this.logger.log('date change: ', date);

      _this.options.url = switchEzopenUrl(_this.options.url, {
        // end= 重置 end 为空
        search: `begin=${dayjs(date).format('YYYYMMDDHHmmss')}&end=`
      });
      _this.event.emit('datePickerChange', {
        data: dayjs(date)
      });
      _this.play({
        url: _this.options.url
      });
    }

    _this.event.on('EVENT_CALLBACK', (data: any) => {
      if (data.data.type === 'appearFirstFrameCallback') {
        removeClass($span, `${PREFIX_CLASS_NAME}-disable`);
      }
    });

    return $span;
  };
}

export default DatePicker;
