const DEFAULT_TIMELINE_OPTIONS = {
  height: 48,
  timeWidth: 0, // 0-3
  time: new Date().getTime(), // new Date("2019-10-31 00:00:00")
  timeSection: []
};

export interface TimeLineOptions {
  timeWidth: number;
  readOnly: any;
  height: number;
  width: number;
  timeSection?: any[];
}

export type TimeLineTimeWidth = 60 | 1800 | 3600 | 86400;

/**
 * @class TimeLine
 * @classdesc 时间轴组件
 * @param {EZUIKitPlayer} params - EZUIKitPlayer 插件对象
 * @example
 * const timeLine = new TimeLine()
 */
class TimeLine {
  options: TimeLineOptions;
  $container: HTMLElement;
  $canvas: HTMLCanvasElement;
  timeWidthTblIndex: number;
  /** canvas context */
  _drawPen: CanvasRenderingContext2D;
  nowTime: number = new Date().getTime();
  timeSection: any[] = [];
  isMouseDown: boolean;
  isOver: boolean;
  mousePosition: number;
  /**  */
  timeWidth: TimeLineTimeWidth = 60;
  oldTime: string | number | Date;
  moved: boolean;
  timeWidthTbls: TimeLineTimeWidth[] = [60, 1800, 3600, 86400];
  timeUnits: string[] = [
    '范围: 1分钟; 单位: 秒',
    '范围: 30分钟; 单位: 分钟',
    '范围: 1小时; 单位: 分钟',
    '范围: 1天; 单位: 小时',
    '范围: 3天; 单位: 小时'
  ];

  timeUnit: string = '';
  mouseTime: number;
  mouseString: string;
  hoverTime: any;
  hoverLeft: number;
  timeTipShow: boolean;

  constructor(container: HTMLElement, options: Partial<TimeLineOptions> = {}) {
    this.options = Object.assign({}, DEFAULT_TIMELINE_OPTIONS, options) as TimeLineOptions;

    if (typeof container === 'string') {
      this.$container = document.getElementById(container) as HTMLElement;
    } else {
      this.$container = container;
    }

    if (!this.$container) {
      throw new Error('[TimeLine]: container is required!');
    }

    this.$canvas = document.createElement('canvas');

    this.$canvas.className = 'time-line';
    this.$canvas.height = this.options.height;
    this.$canvas.width = 500;
    this.$canvas.style.display = 'inline-block;';
    this.$canvas.innerHTML = '该浏览器不支持canvas';
    this.$container.appendChild(this.$canvas);
  }

  init(params: any) {
    const opts = this.options;
    const _this = this;

    return new Promise<void>((resolve, _) => {
      if (params.width) {
        this.$container.setAttribute('width', parseInt(params.width, 10) + 'px');
      }
      // this.randomNum = (Math.random() + '').split('.').join('');
      this.timeWidthTblIndex = opts.timeWidth; // 当前使用时间宽度索引

      this._drawPen = this.$canvas.getContext('2d') as CanvasRenderingContext2D;

      _this.nowTime = params.nowTime; // 当前时间点
      _this.timeSection = opts.timeSection || []; // 时间段记录区间

      _this.update(); // 展示进度条
      // 事件监听
      this.$canvas.addEventListener('mousemove', function (e) {
        if (_this.options.readOnly) {
          return;
        }
        _this.mousemove(e);
      });
      this.$canvas.addEventListener('mouseover', function (e) {
        if (_this.options.readOnly) {
          return;
        }
        _this.mouseover(e);
      });
      this.$canvas.addEventListener('mouseleave', function (e) {
        if (_this.options.readOnly) {
          return;
        }
        _this.mouseleave(e);
      });
      this.$canvas.addEventListener('mousedown', function (e) {
        if (_this.options.readOnly) {
          return;
        }
        _this.mousedown(e);
      });
      this.$canvas.addEventListener('mouseup', function (e) {
        if (_this.options.readOnly) {
          return;
        }
        const callback = params.onChange;
        _this.mouseUpFn(e, callback);
      });

      resolve();
    });
  }

  _renderNode() {}

  subTime(time: number) {
    if (time < 10) {
      return '0' + time;
    } else {
      return time;
    }
  }

  tranTime(time: number | string): string {
    let stringTime = time;
    if (time) {
      const newDate = new Date(time);
      stringTime =
        newDate.getFullYear() +
        '/' +
        (newDate.getMonth() + 1) +
        '/' +
        newDate.getDate() +
        ' ' +
        this.subTime(newDate.getHours()) +
        ':' +
        this.subTime(newDate.getMinutes()) +
        ':' +
        this.subTime(newDate.getSeconds());
    }
    return stringTime + '';
  }

  setWidth(params: any) {
    if (params.width) {
      this.$container.style.width = params.width;
      this.$canvas.width = params.width;
      this.update({ time: this.nowTime || new Date() });
    }
  }

  /**
   * @description 鼠标移动事件
   * @param {MouseEvent} e
   * @returns
   */
  mousemove(e: MouseEvent) {
    if (this.isMouseDown && this.isOver) {
      const mouseOffset = this.mousePosition - e.pageX;
      // fix点击引起mousemove的问题
      if (mouseOffset === 0) {
        return;
      }
      let timeOffsetUnit = 0;
      switch (this.timeWidth) {
        // 每个像素代表的时间
        case 60:
          timeOffsetUnit = 1 / 10;
          break;
        case 1800:
          timeOffsetUnit = (1 / 20) * 60;
          break;
        case 3600:
          timeOffsetUnit = (1 / 20) * 30 * 60;
          break;
        case 86400:
          timeOffsetUnit = (1 / 30) * 60 * 60;
          break;
      }
      const currentTime = new Date(this.oldTime).getTime() + mouseOffset * timeOffsetUnit * 1000;
      this.update({ time: currentTime });
      this.moved = true;
    } else {
      const left = parseInt(this.$container.offsetLeft + '', 10);
      // 12 -10
      this.mousePosition = e.pageX - left;
      this.update(); // 更新画面
    }
  }

  /**
   * @description 鼠标按下事件
   * @param {MouseEvent} e
   */
  mousedown(e: MouseEvent) {
    this.isMouseDown = true;
    this.mousePosition = e.pageX;
    this.oldTime = this.nowTime;
    // this.$emit('drag', true);
  }

  mouseover(_?: MouseEvent) {
    this.isOver = true;
  }

  mouseleave(_?: MouseEvent) {
    this.isOver = false;
    this.isMouseDown = false;
    this.update();
  }

  /**
   * @description 更改时间刻度比例
   * @param {number} timeWidth
   * @return {void}
   * @example
   * timeLine.changeScale(1)  // [60, 1800, 3600, 86400]
   */
  changeScale(timeWidth: 0 | 1 | 2 | 3) {
    this.options.timeWidth = timeWidth;
    this.update({ timeWidth });
  }

  mouseUpFn(_e?: any, callback?: any) {
    if (this.isMouseDown) {
      this.isMouseDown = false;
      if (this.moved) {
        this.moved = false;
        // 限制拖动只更改时分秒
        const newStringTime =
          new Date(this.nowTime).getFullYear() +
          '/' +
          (new Date(this.nowTime).getMonth() + 1) +
          '/' +
          new Date(this.nowTime).getDate() +
          ' ' +
          this.subTime(new Date(this.nowTime).getHours()) +
          ':' +
          this.subTime(new Date(this.nowTime).getMinutes()) +
          ':' +
          this.subTime(new Date(this.nowTime).getSeconds());
        this.nowTime = new Date(newStringTime).getTime();
        this.update({ time: this.nowTime });
        this.oldTime = this.nowTime;
        if (typeof callback === 'function') callback(this.nowTime);
      }
    }
  }

  readOnly() {
    console.log('更改为只读');
    this.options.readOnly = true;
    this.$canvas.style.cursor = 'not-allowed';
  }

  unReadOnly() {
    console.log('更改为只读');
    this.options.readOnly = false;
    this.$canvas.style.cursor = 'pointer';
  }

  /**
   * @description 设置当前播放时间
   * @param {number} time 当前时间戳
   */
  setCurrent(time: number | Date) {
    this.run({
      time
    });
  }

  run(data?: any) {
    if (!this.isMouseDown) {
      this.update(data);
    }
  }

  update(data?: any) {
    const _this = this;
    data = data || {};

    _this.nowTime = data.time || _this.nowTime;
    _this.timeSection = data.timeSection || _this.timeSection;
    _this.timeWidthTblIndex = data.timeWidth || _this.timeWidthTblIndex;
    _this.timeWidth = _this.timeWidthTbls[data.timeWidth || _this.timeWidthTblIndex];
    _this.timeUnit = _this.timeUnits[data.timeWidth || _this.timeWidthTblIndex];
    if (data.timeWidth === 0) {
      _this.timeWidthTblIndex = 0;
      _this.timeWidth = _this.timeWidthTbls[0];
      _this.timeUnit = _this.timeUnits[0];
    }

    this._drawPen.fillStyle = '#000000';
    _this._drawPen.fillRect(0, 0, _this.$canvas.offsetWidth, _this.$canvas.offsetHeight);
    _this.drawScale(); // 画刻度
    _this.drawRecord(); // 画录像区间
    _this.drawOtherMsg(); // 画录像的其他信息
  }

  // prettier-ignore
  drawSolidLine(startX: number, startY: number, endX: number, endY: number, lineWidth: number, color: string) {
    this._drawPen.save();
    this._drawPen.strokeStyle = color;
    this._drawPen.lineWidth = lineWidth;
    this._drawPen.beginPath();
    this._drawPen.moveTo(startX, startY);
    this._drawPen.lineTo(endX, endY);
    this._drawPen.stroke();
    this._drawPen.restore();
  }

  // prettier-ignore
  drawString(text: string, x: number, y: number, align?: CanvasTextAlign) {
    this._drawPen.font = '12px serif';
    this._drawPen.fillStyle = '#ffffff';
    this._drawPen.textAlign = align || 'left';
    this._drawPen.fillText(text, x, y + 10);
  }

  drawScale() {
    const _this = this;
    const lineColor = 'rgba(255,255,255)';
    const startDate = _this.nowTime ? new Date(_this.nowTime) : new Date(); // 开始时间
    let starSecond = startDate.getSeconds(); // 起始的秒数
    let starMin = startDate.getMinutes(); // 起始的分钟数
    let startHours = startDate.getHours(); // 起始的小时
    let startDay = startDate.getDate(); // 起始的日期
    let curScale = 0; // 计算时间点
    switch (_this.timeWidth) {
      case 60: {
        const dotNum = parseInt(_this.$canvas.offsetWidth / 10 + ''); // 每10像素一个点
        startDate.setSeconds(startDate.getSeconds() - parseInt(dotNum / 2 + '', 10)); // 从现在时间的一半开始画起
        startDay = startDate.getDate();
        startHours = startDate.getHours();
        starMin = startDate.getMinutes();
        starSecond = startDate.getSeconds();
        for (let i = 0; i < dotNum; i++) {
          curScale = starSecond + i;
          startDate.setSeconds(curScale);
          // 每一个整10秒画一次线和文字
          if (curScale % 10 === 0) {
            _this.drawSolidLine(
              (i * _this.$canvas.offsetWidth) / dotNum,
              8,
              (i * _this.$canvas.offsetWidth) / dotNum,
              _this.$canvas.offsetHeight / 5 + 8,

              1,
              lineColor
            );
            const timeString =
              this.subTime(startDate.getHours()) +
              ':' +
              this.subTime(startDate.getMinutes()) +
              ':' +
              this.subTime(startDate.getSeconds());
            _this.drawString(
              timeString,
              (i * _this.$canvas.offsetWidth) / dotNum,
              (_this.$canvas.offsetHeight / 5) * 2.5,
              'center',
              'rgba(255,255,255,0.3)'
            );
          } else {
            // 只画一次线
            _this.drawSolidLine(
              (i * _this.$canvas.offsetWidth) / dotNum,
              8,
              (i * _this.$canvas.offsetWidth) / dotNum,
              (_this.$canvas.offsetHeight / 5) * 0.5 + 8,
              1,
              lineColor
            );
          }
          /**
           * 偏移距离超过60，setSeconds会每次累加1到分钟，因此绘图完成后需要复原到当前分钟，再次计算偏移
           */
          startDate.setDate(startDay);
          startDate.setHours(startHours);
          startDate.setMinutes(starMin);
        }
        break;
      }
      case 1800: {
        const nowTime = new Date(_this.nowTime);
        const scaleSeconds =
          nowTime.getHours() * 3600 + nowTime.getMinutes() * 60 + nowTime.getSeconds(); // 偏移秒数
        const scaleMinutes = parseInt(scaleSeconds / 60 + ''); // 偏移分钟数
        const center = {
          position: parseInt(_this.$canvas.offsetWidth / 2 + ''),
          time: 0,
          hideString: true // 中心刻度不渲染时间戳
        }; // 中心点

        // 每20像素一个刻度，一个刻度1分钟（60秒）
        // 每像素3秒
        // 每秒1/3像素
        const dotNum = Math.ceil(_this.$canvas.offsetWidth / 40) + scaleMinutes;

        const scaleArr = []; // 刻度数组（像素值）
        scaleArr.push(center);
        for (let index = 0; index < dotNum; index++) {
          // 获取刻度数组
          scaleArr.push({
            position: center.position + index * 20 - parseInt(scaleSeconds / 3 + ''),
            time: center.time + index * 60
          });
          scaleArr.unshift({
            position: center.position - index * 20 - parseInt(scaleSeconds / 3 + ''),
            time: center.time - index * 60
          });
        }
        for (let index = 0; index < scaleArr.length; index++) {
          // 画刻度、时间文本
          _this.drawSolidLine(
            scaleArr[index].position,
            8,
            scaleArr[index].position,
            (_this.$canvas.offsetHeight / 5) * (scaleArr[index].time % 100 === 0 ? 1 : 0.5) + 8,
            1,
            lineColor
          );
          if (scaleArr[index].time % 100 === 0 && !scaleArr[index].hideString) {
            const date = new Date(scaleArr[index].time * 1000); // 转换为毫秒
            const hours = date.getUTCHours() < 10 ? `0${date.getUTCHours()}` : date.getUTCHours();
            const minutes =
              date.getUTCMinutes() < 10 ? `0${date.getUTCMinutes()}` : date.getUTCMinutes();
            // const secondsStr =
            //   date.getUTCSeconds() < 10 ? `0${date.getUTCSeconds()}` : date.getUTCSeconds();
            const timeStr = `${hours}:${minutes}`;
            _this.drawString(
              timeStr,
              scaleArr[index].position,
              (_this.$canvas.offsetHeight / 5) * 2,
              'center',
              'rgba(255,255,255,0.3)'
            );
          }
        }

        break;
      }
      case 3600: {
        const nowTime = new Date(_this.nowTime);
        const scaleSeconds =
          nowTime.getHours() * 3600 + nowTime.getMinutes() * 60 + nowTime.getSeconds(); // 偏移秒数
        const scaleHalfHours = parseInt(scaleSeconds / 1800 + ''); // 偏移半小时数
        const center = {
          position: parseInt(_this.$canvas.offsetWidth / 2 + ''),
          time: 0,
          hideString: true // 中心刻度不渲染时间戳
        }; // 中心点

        // 每20像素一个刻度，一个刻度半小时（1800秒）
        // 每像素90秒
        // 每秒1/90像素
        const dotNum = Math.ceil(_this.$canvas.offsetWidth / 40) + scaleHalfHours;

        const scaleArr = []; // 刻度数组（像素值）
        scaleArr.push(center);
        for (let index = 0; index < dotNum; index++) {
          // 获取刻度数组
          scaleArr.push({
            position: center.position + index * 20 - parseInt(scaleSeconds / 90 + ''),
            time: center.time + index * 1800
          });
          scaleArr.unshift({
            position: center.position - index * 20 - parseInt(scaleSeconds / 90 + ''),
            time: center.time - index * 1800
          });
        }
        for (let index = 0; index < scaleArr.length; index++) {
          // 画刻度、时间文本
          _this.drawSolidLine(
            scaleArr[index].position,
            8,
            scaleArr[index].position,
            (_this.$canvas.offsetHeight / 5) * (scaleArr[index].time % 9000 === 0 ? 1 : 0.5) + 8,
            1,
            lineColor
          );
          if (scaleArr[index].time % 9000 === 0 && !scaleArr[index].hideString) {
            const date = new Date(scaleArr[index].time * 1000); // 转换为毫秒
            const hours = date.getUTCHours() < 10 ? `0${date.getUTCHours()}` : date.getUTCHours();
            const minutes =
              date.getUTCMinutes() < 10 ? `0${date.getUTCMinutes()}` : date.getUTCMinutes();
            // const secondsStr =
            //   date.getUTCSeconds() < 10 ? `0${date.getUTCSeconds()}` : date.getUTCSeconds();
            const timeStr = `${hours}:${minutes}`;
            _this.drawString(
              timeStr,
              scaleArr[index].position,
              (_this.$canvas.offsetHeight / 5) * 2,
              'center',
              'rgba(255,255,255,0.3)'
            );
          }
        }
        break;
      }
      case 86400: {
        const nowTime = new Date(_this.nowTime);
        const scaleSeconds =
          nowTime.getHours() * 3600 + nowTime.getMinutes() * 60 + nowTime.getSeconds(); // 偏移秒数
        const scaleHours = parseInt(scaleSeconds / 3600 + ''); // 偏移小时数
        const center = {
          position: parseFloat(_this.$canvas.offsetWidth / 2 + ''),
          time: 0,
          hideString: true // 中心刻度不渲染时间戳
        }; // 中心点

        // 每30像素一个刻度，一个刻度1小时（3600秒）
        // 每像素120秒
        // 每秒1/120像素
        const dotNum = Math.ceil(_this.$canvas.offsetWidth / 60) + scaleHours;

        const scaleArr = []; // 刻度数组（像素值）
        scaleArr.push(center);
        for (let index = 0; index < dotNum; index++) {
          // 获取刻度数组
          scaleArr.push({
            position: center.position + index * 30 - scaleSeconds / 120,
            time: center.time + index * 3600
          });
          scaleArr.unshift({
            position: center.position - index * 30 - scaleSeconds / 120,
            time: center.time - index * 3600
          });
        }
        for (let index = 0; index < scaleArr.length; index++) {
          // 画刻度、时间文本
          _this.drawSolidLine(
            scaleArr[index].position,
            8,
            scaleArr[index].position,
            (_this.$canvas.offsetHeight / 5) * (scaleArr[index].time % 7200 === 0 ? 1 : 0.5) + 8,
            1,
            lineColor
          );
          if (scaleArr[index].time % 7200 === 0 && !scaleArr[index].hideString) {
            const date = new Date(scaleArr[index].time * 1000); // 转换为毫秒
            const hours = date.getUTCHours() < 10 ? `0${date.getUTCHours()}` : date.getUTCHours();
            const minutes =
              date.getUTCMinutes() < 10 ? `0${date.getUTCMinutes()}` : date.getUTCMinutes();
            // const secondsStr =
            //   date.getUTCSeconds() < 10 ? `0${date.getUTCSeconds()}` : date.getUTCSeconds();
            const timeStr = `${hours}:${minutes}`;
            _this.drawString(
              timeStr,
              scaleArr[index].position,
              (_this.$canvas.offsetHeight / 5) * 2,
              'center',
              'rgba(255,255,255,0.3)'
            );
          }
        }

        break;
      }
    }
  }

  /**
   * @description 设置片段
   * @param timeArr
   */
  setRecord(timeArr: any[]) {
    this.timeSection = timeArr;
    this.drawRecord();
  }

  drawRecord() {
    // 绘制蓝色区域
    const timeArr = this.timeSection || [];
    const _this = this;
    const drawPen = this._drawPen;

    const arr = [];

    // 根据时间查找当前位置
    for (let i = 0; i < timeArr.length; i++) {
      const startPosition = findPosition(timeArr[i].startTime);
      const endPosition = findPosition(timeArr[i].endTime);
      drawPen.fillStyle = '#1890ff80';
      drawPen.fillRect(startPosition, 0, endPosition - startPosition, 48);
      arr.push([startPosition, 0, endPosition - startPosition, 48]);
    }

    function findPosition(time: number) {
      let scale = 10;
      switch (_this.timeWidth) {
        case 60:
          scale = 10;
          break;
        case 1800:
          scale = 20 / 60;
          break;
        case 3600:
          scale = 20 / 30 / 60;
          break;
        case 86400:
          scale = 30 / 60 / 60;
          break;
      }
      const nowTimePosition = _this.$canvas.offsetWidth / 2; // 总宽度一半
      let position = nowTimePosition + ((time - _this.nowTime) / 1000) * scale;
      if (position > _this.$canvas.offsetWidth) {
        position = _this.$canvas.offsetWidth;
      }
      if (position <= 0) {
        position = 0;
      }
      return position;
    }
  }

  drawOtherMsg() {
    // 画中心线
    this.drawSolidLine(
      this.$canvas.offsetWidth / 2,
      0,
      this.$canvas.offsetWidth / 2,
      this.$canvas.offsetHeight,
      2,
      '#1890FF'
    );

    this._drawPen.shadowBlur = 0;

    if (this.isOver && !this.isMouseDown) {
      this.mouseTime =
        (this.mousePosition / this.$canvas.offsetWidth) * this.timeWidth * 1000 +
        this.nowTime -
        (this.timeWidth / 2) * 1000; // 鼠标的悬浮点对应的时间

      this.mouseString = this.tranTime(this.mouseTime); // 鼠标悬浮点显示的文字
      this.hoverTime = this.mouseString;
      this.hoverLeft = this.mousePosition - 60;
      this.timeTipShow = true;
    } else {
      this.timeTipShow = false;
    }
  }
}

export default TimeLine;
