import dayjs from 'dayjs';
import './index.scss';
import { PREFIX_CLASS_NAME } from '@ezuikit/utils-dom';

const noop = () => {};

type CallBack = (date?: string) => void;

interface MobileTimeLineOptions {
  fragmentColor?: string;
  lineBgColor?: string;
  scaleLineColor?: string;
  onCurrent?: CallBack;
  onChange?: CallBack;
  ontouchstart?: (e?: TouchEvent) => void;
  ontouchmove?: (e?: TouchEvent) => void;
  ontouchend?: (e?: TouchEvent) => void;
}

const DEFAULT_OPTIONS = {
  onCurrent: noop,
  onChange: noop,
  fragmentColor: '#A8B9ED',
  lineBgColor: '#ddd',
  scaleLineColor: '#ccc'
};

const PREFIX_CLS = `${PREFIX_CLASS_NAME}-timeline-mobile`;

const scaleList = [60, 600, 1800, 3600, 43200];

/**
 * @class MobileTimeLine
 * @classdesc 时间轴组件
 * @param {EZUIKitPlayer} params - EZUIKitPlayer 插件对象
 * @example
 * const timeLine = new TimeLine($container, {})
 */
class MobileTimeLine {
  options: MobileTimeLineOptions;
  state: {
    start: string; // 传入最近片段起始时刻
    end: string; // 传入最近片段结束时刻 默认结束时间为24：00：00, 1440  24
    current: string; // 当前播放时刻
    /** 刻度间隔 单位分钟 120: 2小时， 60：1小时， 30：半小时，10：10分钟，1：1分钟 */
    scale: number; // 120: 2小时， 60：1小时， 30：半小时，10：10分钟，1：1分钟
    timeArr: Array<{ id: number; current: any; recArr: any[] }>; // 时间轴列表
    availTimeLine: Array<{ startTime: string; endTime: string }>; // 由实际存在视频片段的时间组成，[{startTime: '', endTime: ''}, {startTime: '', endTime: ''}, ...]
    scrollTop: number; // 页面滚动偏移量 页面偏移量由传入时刻决定
    index: number; // 初始时刻在availTimeLine中的index
    date: string;
    noTimeLineTxt: string;
    disabled: boolean; // 禁用拖动轴
  };

  onChange: CallBack;

  /** 拖拽中 */
  dragging = false;

  $container: HTMLElement;

  constructor(container: HTMLElement, options: MobileTimeLineOptions) {
    if (!(container instanceof HTMLElement)) {
      throw new Error('[MobileTimeLine] container must be a HTMLElement!');
    }
    this.$container = container;
    this.options = Object.assign({}, DEFAULT_OPTIONS, options || {});
    this.onChange = options?.onChange || noop;

    this.state = {
      start: '00:00:00', // 传入最近片段起始时刻
      end: '24:00:00', // 传入最近片段结束时刻 默认结束时间为24：00：00, 1440  24
      current: '00:00:00', // 当前播放时刻
      scale: 30, // 120: 2小时， 60：1小时， 30：半小时，10：10分钟，1：1分钟
      timeArr: [], // 时间轴列表
      availTimeLine: [], // 由实际存在视频片段的时间组成，[{startTime: '', endTime: ''}, {startTime: '', endTime: ''}, ...]
      scrollTop: 0, // 页面滚动偏移量 页面偏移量由传入时刻决定
      index: 0, // 初始时刻在availTimeLine中的index
      date: '',
      noTimeLineTxt: '',
      disabled: false // 禁用拖动轴
    };

    this._touchEvent();
    this.matchTimeDot();
  }

  /** touch event */
  _touchEvent() {
    const _this = this;

    /** 开始拖动 */
    function _ontouchstart(e: TouchEvent) {
      if (_this.state.disabled) {
        return;
      }
      _this.dragging = true;
      if (typeof _this.options.ontouchstart === 'function') _this.options.ontouchstart(e);
    }

    /** 拖动中 */
    function _ontouchmove(e: TouchEvent) {
      if (_this.state.disabled) {
        return;
      }
      _this.dragging = true;
      if (typeof _this.options.ontouchmove === 'function') _this.options.ontouchmove(e);
    }

    /** 拖动结束 */
    function _ontouchend(e: TouchEvent) {
      if (_this.state.disabled) {
        return;
      }
      _this.dragging = false;
      //
      let timer: number;
      const scrollPromise = new Promise(function (resolve, _reject) {
        let preTop = -1;
        timer = window.setInterval(() => {
          const reactTop = (_this.$container?.parentNode as any).scrollTop; // 实际偏移高度
          if (reactTop !== preTop) {
            console.log('scrolling', reactTop, preTop);
            preTop = reactTop;
          } else {
            console.log('scroll stop', reactTop, preTop);
            clearInterval(timer);
            resolve(reactTop);
          }
        }, 100);
      });

      //
      void scrollPromise.then((reactTop) => {
        _this.rectTopToTime(reactTop);
        console.log('页面滚动实际高度', reactTop, _this.state);
        _this.onChange(_this.state.current);
      });

      if (typeof _this.options.ontouchend === 'function') _this.options.ontouchend(e);
    }

    if (this.checkIsHarmonyOS()) {
      // js安卓、IOS、鸿蒙系统监听开始触摸
      // 监听手动滚动时间轴时，停止时间轴滚动，时刻仍然变化
      // prettier-ignore
      this.$container.parentNode?.addEventListener('touchstart', _ontouchstart, false);

      // 监听手动滚动时间轴时，停止时间轴滚动，时刻仍然变化
      // prettier-ignore
      this.$container.parentNode?.addEventListener('touchmove', _ontouchmove, false);

      // 手动滚动停止，选定时刻自动播放
      // prettier-ignore
      this.$container.parentNode?.addEventListener('touchend', _ontouchend, false);
    } else {
      // 监听手动滚动时间轴时，停止时间轴滚动，时刻仍然变化
      (_this.$container?.parentNode as any).ontouchstart = _ontouchstart;
      // 监听手动滚动时间轴时，停止时间轴滚动，时刻仍然变化
      (_this.$container?.parentNode as any).ontouchmove = _ontouchmove;
      // 手动滚动停止，选定时刻自动播放
      (_this.$container.parentNode as any).ontouchend = _ontouchend;
    }
  }

  setState(obj: any) {
    Object.keys(obj).forEach((key) => {
      (this.state as any)[key] = obj[key];
      if (key === 'scrollTop') {
        (this.$container?.parentNode as any).scrollTo(0, obj[key]);
      }
      if (key === 'current') {
        if (typeof this.options.onCurrent === 'function') {
          this.options.onCurrent(obj[key]);
        }
      }
    });
  }

  setDisabled(value: any) {
    this.setState({
      disabled: value
    });
    if (this.$container?.parentNode)
      (this.$container.parentNode as any).style.overflowY = value ? 'hidden' : 'scroll';
    // console.log(" this.$container.parentNode.style", this.$container.parentNode)
  }

  /**
   * @description 格式化时间转秒
   * @param {string} time 时间 （01:05:50）
   * @returns {number}
   * @example
   * timeToSecond("01:05:50") // => 3950
   * timeToSecond("01:05") // => 3900
   */
  timeToSecond(time: string) {
    const e = time.split(':');
    const h = Number(e[0]);
    const m = Number(e[1]);
    const s = Number(e[2]); //
    return h * 60 * 60 + m * 60 + (s ? s : 0);
  }

  minuteToTime(minute: number) {
    const hour = Math.floor(minute / 60);
    const m = minute % 60;
    return (hour > 9 ? hour : '0' + hour) + ':' + (m > 9 ? m : '0' + m);
  }

  /**
   * @description 格式化时间转分钟
   * @param {string} time 时间 （01:05）
   * @returns {number}
   * @example
   * timeToSecond("01:05") // => 65
   */
  timeToMinute(time: string) {
    const e = time.split(':');
    const h = Number(e[0]);
    const m = Number(e[1]);
    // const s = Number(e[2]);
    return h * 60 + m;
  }

  checkIsAppleDevice() {
    const u = navigator.userAgent;
    const ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    const iPad = u.includes('iPad');
    const iPhone = u.includes('iPhone') || u.includes('Mac');
    if (ios || iPad || iPhone) {
      return true;
    } else {
      return false;
    }
  }

  checkIsHarmonyOS() {
    const u = navigator.userAgent;
    if (u.includes('ohos')) {
      return true;
    } else {
      return false;
    }
  }

  matchTimeDot() {
    const { start, end, scale } = this.state;
    console.log('start', start, 'end', end);
    const timeArr = [];

    // 时间转分钟
    let minute = this.timeToMinute(end);
    // 检测是否包含秒,则实际分钟加1
    minute = Math.floor(minute / scale) * scale;

    for (let i = minute; i >= 0; ) {
      let marginTop = 0;
      let marginBottom = 0;
      // 视频片段
      const recArr: never[] = [];
      if (i === minute) {
        marginTop = 70;
      }
      if (i === 0) {
        marginBottom = 230;
      }
      // for (let j = 0; j < len; j++) {
      //   if (i >= availArr[j].startTime && i <= availArr[j].endTime) {
      //     // borderColor = '#f7a670';
      //     // console.log("i",i,availArr[j])
      //     if (i == availArr[j].startTime && i == availArr[j].endTime) {
      //       var height = availArr[j].etAvailPercent - availArr[j].stAvailPercent;
      //       var top = 60 - availArr[j].etAvailPercent;
      //       recArr.push({
      //         height: height,
      //         top: top
      //       })
      //     } else {
      //       if (i == availArr[j].startTime) {
      //         recArr.push({
      //           height: 60 - availArr[j].stAvailPercent,
      //           top: 0
      //         })
      //       } else if (i == availArr[j].endTime) {
      //         recArr.push({
      //           height: availArr[j].etAvailPercent,
      //           top: 60 - availArr[j].etAvailPercent,
      //         })
      //       } else if (i > availArr[j].startTime && i < availArr[j].endTime) {
      //         // 跨越多个区域
      //         recArr.push({
      //           height: 60,
      //           top: 0,
      //         })
      //       }
      //     }
      //   }

      // }
      const time = this.minuteToTime(i);
      timeArr.push({
        id: i,
        current: time,
        label: 'a' + i,
        marginTop,
        marginBottom,
        recArr
      });
      i = i - scale;
    }
    this.setState({
      timeArr
    });
    this.renderDateLine();
  }

  /**
   * @description 更改轴比例
   * @param value
   */
  changeScale(value: 0 | 1 | 2 | 3) {
    const scale = scaleList[value] || 30;
    this.setState({
      scale // 120: 2小时， 60：1小时， 30：半小时，10：10分钟，1：1分钟
    });
    this.matchTimeDot();
  }

  /**
   *
   * @param news
   * @param defaultIndex
   */
  setRecord(news: any, defaultIndex?: any) {
    if (news.length > 0) {
      if (typeof defaultIndex === 'undefined') {
        defaultIndex = news.length - 1;
      }
      this.setState({
        availTimeLine: news,
        start: news[defaultIndex].startTime,
        end: news[defaultIndex].endTime,
        current: news[defaultIndex].startTime
      });

      this.matchRecTimeDot();

      this.primaryOffsetH();
      // 将当前播放时间片段传给父组件
      // this.onChange(news[defaultIndex].startTime);
    } else {
      this.setState({
        availTimeLine: []
      });
      console.log('this.state', this.state);
      this.matchRecTimeDot();
    }
  }

  matchRecTimeDot() {
    const { start, end, scale, availTimeLine, timeArr } = this.state;
    console.log('start', start, 'end', end);
    // 播放时间片段时刻转分钟
    const availArr = [];
    const len = availTimeLine.length;
    if (len === 0) {
      for (let j = 0; j < timeArr.length; j++) {
        timeArr[j].recArr = [];
      }
    } else {
      // xuehb 切换回放时要把上一次的回放时间段重置清空掉
      for (let k = 0; k < timeArr.length; k++) {
        timeArr[k].recArr = [];
      }

      for (let i = 0; i < len; i++) {
        const temp = availTimeLine[i];
        const startTime = this.timeToSecond(temp.startTime);
        const endTime = this.timeToSecond(temp.endTime);
        // console.log('startTime: ', startTime, 'endTime:', endTime);
        let stAvailPercent = 0;
        let etAvailPercent = 0;
        const stMinute = Math.floor(startTime / (scale * 60)) * scale;
        stAvailPercent = (startTime - stMinute * 60) / scale;
        const etMinute = Math.floor(endTime / (scale * 60)) * scale;
        etAvailPercent = (endTime - etMinute * 60) / scale;
        availArr[i] = {
          startTime: stMinute,
          endTime: etMinute,
          stAvailPercent,
          etAvailPercent
        };
        for (let j = 0; j < timeArr.length; j++) {
          if (timeArr[j].id === stMinute && timeArr[j].id === etMinute) {
            const height = etAvailPercent - stAvailPercent;
            const top = 60 - etAvailPercent;
            timeArr[j].recArr.push({
              height,
              top
            });
          } else {
            if (timeArr[j].id === stMinute) {
              timeArr[j].recArr.push({
                height: 60 - stAvailPercent,
                top: 0
              });
            } else if (timeArr[j].id === etMinute) {
              timeArr[j].recArr.push({
                height: etAvailPercent,
                top: 60 - etAvailPercent
              });
            } else if (timeArr[j].id > stMinute && timeArr[j].id < etMinute) {
              timeArr[j].recArr.push({
                height: 60,
                top: 0
              });
            }
          }
        }
      }
    }

    this.setState({
      timeArr
    });
    console.log('timeArr:', this.state);
    this.renderDateLine();
  }

  /**
   * @description 渲染时间线
   */
  renderDateLine() {
    const { timeArr } = this.state;
    this.$container.innerHTML = '';
    timeArr.forEach((item: any) => {
      const timeItemDOM = document.createElement('div');
      timeItemDOM.setAttribute('class', `${PREFIX_CLS}-time-item`);
      (
        timeItemDOM as any
      ).style = `margin-top: ${item.marginTop}px; margin-bottom: ${item.marginBottom}px; border-right-color: ${this.options.lineBgColor};`;
      // 6个刻度
      let timeItemHtml = `<div class="${PREFIX_CLS}-scale" style="border-bottom-color: ${this.options.scaleLineColor};"></div>
                          <div class="${PREFIX_CLS}-scale" style="border-bottom-color: ${this.options.scaleLineColor};"></div>
                          <div class="${PREFIX_CLS}-scale" style="border-bottom-color: ${this.options.scaleLineColor};"></div>
                          <div class="${PREFIX_CLS}-scale" style="border-bottom-color: ${this.options.scaleLineColor};"></div>
                          <div class="${PREFIX_CLS}-scale" style="border-bottom-color: ${this.options.scaleLineColor};"></div>
                          <div class="${PREFIX_CLS}-scale" style="width:10px; border-bottom-color: ${this.options.scaleLineColor};"></div>`;
      item.recArr.forEach((i: any) => {
        timeItemHtml += `<div class="${PREFIX_CLS}-valid" style="height: ${i.height}px;background-color:${this.options.fragmentColor}; top: ${i.top}px"></div>`;
      });
      timeItemHtml += `<div id=${item.label} style="position: relative; top: 51px; left: 40%"> ${item.current}</div>`;
      timeItemDOM.innerHTML = timeItemHtml;
      this.$container.appendChild(timeItemDOM);
    });
  }

  primaryOffsetH() {
    const { start, scale, timeArr } = this.state;
    const currentItem = timeArr[0].current;
    const currentTime = this.timeToSecond(currentItem);
    const startSecond = this.timeToSecond(start);
    const offsetS = currentTime - startSecond;
    const offsetH = Math.ceil(offsetS / scale) + 60; // offsetS / (scale * 60) * 60
    this.setState({
      scrollTop: offsetH
    });
    console.log('起始偏移量', offsetH);
  }

  currentOffsetH() {
    const { current, scale, timeArr } = this.state;
    const startItem = timeArr[0].current;
    const startSecond = this.timeToSecond(startItem);
    const currentSecond = this.timeToSecond(current);
    const offsetS = startSecond - currentSecond;
    const offsetH = Math.ceil(offsetS / scale) + 60; // offsetS / (scale * 60) * 60
    this.setState({
      scrollTop: offsetH
    });
  }

  rectTopToTime(reactTop: any) {
    const { scale } = this.state;
    // let rectTop = rect.top; // 获取当前元素距离父元素顶部的高度
    // let reactTop = 0 - rectTop; // 实际偏移高度
    const index = Math.floor(reactTop / 60); // 以分钟为刻度时，每个元素初始高度为60px, 向下取整并除以时刻倍数得出偏移item;
    const offsetH = reactTop - index * 60; // 偏移高度
    let current;
    let offsetSecond;
    console.log('index：', index, 'offsetH:', offsetH);
    if (offsetH === 0) {
      current = this.state.timeArr[index - 1].current;
      offsetSecond = 0;
    } else {
      // 当scale==120,scale==60,scale==30,scale==10,scale==1
      const time = this.state.timeArr[index].current;
      const minute = this.timeToMinute(time);
      // 相对于下一元素偏移
      const offsetY = 60 - offsetH;
      const offsetS = offsetY * scale; // offsetY / 60 * scale * 60
      const offsetM = Math.floor(offsetS / 60) + minute;
      const second = Math.floor(offsetS / 60) * 60;
      offsetSecond = Math.ceil(offsetS - second); // 保留两位小数
      current = this.minuteToTime(offsetM);
    }
    this.setState({
      current: current + ':' + (offsetSecond > 9 ? offsetSecond : '0' + offsetSecond),
      scrollTop: reactTop
    });
    // console.log("rectLeft", reactTop);
    // console.log("currentTime", current + ':' + (offsetSecond > 9 ? offsetSecond : '0' + offsetSecond));
  }

  /**
   * @description 设置当前播放时间
   * @description
   * @param {string} time 当前时间 （HH:mm:ss）
   */
  setCurrent(time: string) {
    if (this.dragging) {
      return;
    }

    // 兼容 时间戳
    if (typeof time === 'number') {
      time = dayjs(time).format('HH:mm:ss');
    }

    this.setState({
      current: time
    });
    this.currentOffsetH();
  }

  secondCountDown() {
    const { current } = this.state;
    // console.log('currentTime', current);
    const temp = current.split(':');
    const hour = Number(temp[0]);
    const minute = Number(temp[1]);
    const second = Number(temp[2]);
    const t = hour * 60 * 60 + minute * 60 + second + 1;
    const h = Math.floor(t / 3600);
    const m = Math.floor((t - h * 3600) / 60);
    const s = t - h * 3600 - m * 60;
    this.setState({
      current: (h > 9 ? h : '0' + h) + ':' + (m > 9 ? m : '0' + m) + ':' + (s > 9 ? s : '0' + s)
    });
  }
}

export default MobileTimeLine;
