/**
 * @description 时间戳格式化 hh:mm:ss
 * @param {number} now 时间戳
 * @returns {string}
 */
export function format(now: number) {
  const time = new Date(now);
  const h = time.getHours(); // 返回日期中的小时数（0到23）
  const m = time.getMinutes(); // 返回日期中的分钟数（0到59）
  const s = time.getSeconds(); // 返回日期中的秒数（0到59）
  return (h > 9 ? h : '0' + h) + ':' + (m > 9 ? m : '0' + m) + ':' + (s > 9 ? s : '0' + s);
}
