/**
 * @description 全屏旋转90°
 * @param {HTMLElement} element
 * @param {number} zIndex
 * @returns
 */
export function requestMobileFullScreen(element: HTMLElement, zIndex: number = 9999) {
  return new Promise<void>(() => {
    const width = document.documentElement.clientWidth;
    const height = document.documentElement.clientHeight;
    const wrapper = element;
    let style = '';
    style += 'width:' + height + 'px;'; // 注意旋转后的宽高切换
    style += 'height:' + width + 'px;';
    style += '-webkit-transform: rotate(90.001deg); transform: rotate(90.001deg);';
    // 注意旋转中点的处理
    style += '-webkit-transform-origin: ' + width / 2 + 'px ' + width / 2 + 'px;';
    style += 'transform-origin: ' + width / 2 + 'px ' + width / 2 + 'px;';
    style += `position: fixed;top: 0;left: 0;z-index:${zIndex}`;
    wrapper.style.cssText = wrapper.style.cssText + style;
  });
}

/**
 * @description 取消全屏旋转90°
 * @param {HTMLElement} element
 * @param {number} width
 * @param {number} height
 */
export function cancelMobileFullScreen(element: HTMLElement, width: number, height: number) {
  return new Promise<void>(() => {
    let style = '';
    style += 'width:' + width + 'px;';
    style += 'height:' + height + 'px;';
    style += '-webkit-transform: none; transform: none;';
    style += '-webkit-transform-origin: 0 0;';
    style += 'transform-origin: 0 0;';
    element.style.cssText = element.style.cssText + style;
  });
}
