import TimeLine from './pc';
import MobileTimeLine from './mobile';
import { PREFIX_CLASS_NAME, style } from '@ezuikit/utils-dom';
import {
  FOOTER_BOTTOM_LEFT_CLS,
  FOOTER_MOBILE_EXTEND_TIMELINE,
  FOOTER_PREFIX_CLS
} from '@ezuikit/theme-plugin-footer';
import { format } from './mobile/utils';
import { delegationClick } from '../../utils/delegationClick';
import dayjs, { type Dayjs } from 'dayjs';
import { switchEzopenUrl } from '@ezuikit/utils-tools';

// interface

function TimeLineLoader() {
  return function (theme: any) {
    const _this = theme;

    let TIMELINE_PREFIX_CLS = `${PREFIX_CLASS_NAME}-timeline`;

    const $container = document.createElement('div');
    $container.classList.add(`${TIMELINE_PREFIX_CLS}-container`);
    $container.style.display = 'flex';

    let _timer: number = null!;
    let list: any[] = [];
    let _seekTime = 0; // 时间戳
    /** 1： 拖拽 <- (鼠标方向)  2： 拖拽 -> (鼠标方向) */
    let _seekType = 0; // 时间戳

    let timeLine: MobileTimeLine | TimeLine;

    let deviceCapacity = {};

    let ended = false;

    // FIXME: H5的时间只支持一天内数据 因为时间轴是根据 HH:mm:ss 来绘制和定位的
    if (_this.options.isMobile) {
      TIMELINE_PREFIX_CLS = `${TIMELINE_PREFIX_CLS}-mobile`;

      $container.innerHTML = `
        <div class="${TIMELINE_PREFIX_CLS}-container">
          <div class="${TIMELINE_PREFIX_CLS}-current">
            <div class="${TIMELINE_PREFIX_CLS}-current-time-bg" id="time-line-current">00:00:00</div>
          </div>
          <div class="${TIMELINE_PREFIX_CLS}-item-container">
            <div class="${TIMELINE_PREFIX_CLS}-item"></div>
          </div>
        </div>
      `;
      // prettier-ignore
      const $extendTimeline = (_this.$container as HTMLElement).querySelector(`.${FOOTER_MOBILE_EXTEND_TIMELINE}`);
      if ($extendTimeline) {
        $extendTimeline.appendChild($container);
        // prettier-ignore
        timeLine = new MobileTimeLine($container.querySelector(`.${TIMELINE_PREFIX_CLS}-item`) as HTMLElement,
          {
            onCurrent: (time: string) => {
              const $current = ($container.querySelector(`.${TIMELINE_PREFIX_CLS}-current-time-bg`)as HTMLElement)
              if ($current) $current.innerHTML = time
            },
            onChange: (time: any) => {
              const begin = _this.urlInfo.searchParams?.begin
              if (list?.length || begin) {
                _this.logger.log(`[MobileTimeLine]: onChange `, time)
                const day = dayjs(list[0]?.startTime || begin || dayjs()).format("YYYY-MM-DD")
                seek(dayjs(`${day} ${time}`))
              }
            },
          }
        );
      }
    } else {
      // PC
      const $scaleControls = document.createElement('div');
      style($scaleControls, {
        display: 'inline-flex',
        flexDirection: 'column',
        justifyContent: 'center',
        verticalAlign: 'top',
        padding: '0 20px'
      });

      $scaleControls.innerHTML = `
            <span style="vertical-Align: middle;line-height: 14px;height: 18px; width: 18px;cursor:pointer;" class="${PREFIX_CLASS_NAME}-timeline-scale-add">
            <svg fill="#2C2C2C" version="1.1" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
              <g>
                <polygon points="0.1,0.5 15,0.5 15,15.4 0.1,15.4 	"></polygon>
              </g>
              <g>
                <path fill="#FFFFFF" d="M7.6,12.4c-0.3,0-0.5-0.2-0.5-0.5v-8c0-0.3,0.2-0.5,0.5-0.5s0.5,0.2,0.5,0.5v8C8.1,12.2,7.9,12.4,7.6,12.4z"></path>
              </g>
              <g>
                <path fill="#FFFFFF"  d="M11.6,8.4h-8c-0.3,0-0.5-0.2-0.5-0.5s0.2-0.5,0.5-0.5h8c0.3,0,0.5,0.2,0.5,0.5S11.8,8.4,11.6,8.4z"></path>
              </g>
            </svg>
            </span>
            <span style="vertical-Align: middle;line-height: 14px;height: 18px; width: 18px;cursor:pointer;" class="${PREFIX_CLASS_NAME}-timeline-scale-sub">
            <svg fill="#2C2C2C" version="1.1" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
              <g>
                <polygon points="1,0.8 15.2,0.8 15.2,15 1,15"></polygon>
              </g>
              <g>
                <path fill="#FFFFFF" d="M12.1,8.4h-8c-0.3,0-0.5-0.2-0.5-0.5s0.2-0.5,0.5-0.5h8c0.3,0,0.5,0.2,0.5,0.5S12.4,8.4,12.1,8.4z"></path>
              </g>
            </svg>
            </span>
      `;

      timeLine = new TimeLine($container);
      $container.append($scaleControls);

      let size: 0 | 1 | 2 | 3 = 0;

      delegationClick($scaleControls, `.${PREFIX_CLASS_NAME}-timeline-scale-add`, (_e) => {
        ++size;
        if (size >= 3) {
          size = 3;
        }
        (timeLine as TimeLine).changeScale(size as 0 | 1 | 2 | 3);
        console.log(_this.getOSDTime());
      });
      delegationClick($scaleControls, `.${PREFIX_CLASS_NAME}-timeline-scale-sub`, (_e) => {
        --size;
        if (size <= 0) {
          size = 0;
        }
        (timeLine as TimeLine).changeScale(size as 0 | 1 | 2 | 3);
      });

      // prettier-ignore
      const $footerBottomLeft = (_this.$container as HTMLElement).querySelector(`.${FOOTER_BOTTOM_LEFT_CLS}`);
      if ($footerBottomLeft) {
        timeLine
          .init({
            width: getCanvasContainerWidth(),
            newTime: null,
            onChange: (date: any) => {
              _this.logger.log(`[TimeLine]: onChange `, date);
              seek(date);
            }
          })
          .then(() => {
            (timeLine as TimeLine).setCurrent(new Date(new Date().setHours(0, 0, 0, 0)));
            _this.logger.log('[TimeLine] init finished');
          })
          .catch(() => {});
        $footerBottomLeft.appendChild($container);
        timeLine.setWidth({ width: getCanvasContainerWidth() });
      }
    }

    function getCanvasContainerWidth() {
      // 由于dom 隐藏然后获取dom的宽为NaN
      // prettier-ignore
      return parseInt(getComputedStyle(_this.$container.querySelector(`.${FOOTER_PREFIX_CLS}`)).width,10) - 58 - 40;
    }

    /**
     *
     * @param date
     * @returns
     */
    function seek(date: Date | Dayjs) {
      const FORMAT = 'YYYYMMDDTHHmmss';
      let startDate = dayjs(date);

      console.log(`%c${startDate.format('YYYY-MM-DD HH:mm:ss')}`, 'font-size: 20px; color: red;');

      // 不允许跨天
      const beginTime = dayjs(_this.urlInfo.searchParams?.begin || dayjs());
      const endDate =
        list.length || _this.urlInfo.searchParams?.end
          ? dayjs(_this.urlInfo.searchParams?.end || list[list.length - 1]?.endTime)
          : dayjs(beginTime.format('YYYY-MM-DD 23:59:59'));
      _seekTime = startDate.valueOf();
      _seekType = _seekTime > (_this.getOSDTime() || 0) * 1000 ? 1 : 2;
      _this.event.emit('timeLineChange', _seekTime);

      if (startDate >= endDate) {
        _seekTime = 0;
        _seekType = 0;
        clearInterval(_timer);
        console.log('开始时间大于结束时间 结束');
        _this.stop();
        ended = true;
        (timeLine as TimeLine).setCurrent(endDate.valueOf());
        _this.message.showError('查找录像开始时间错误');
        return;
      }

      if (ended) {
        _this.play({
          url: switchEzopenUrl(_this.options.url, {
            search: `begin=${startDate.format('YYYYMMDDHHmmss')}`
          })
        });
        ended = false;
        return;
      }

      if (list[0] && startDate <= dayjs(list[0].startTime)) {
        (timeLine as TimeLine).setCurrent(dayjs(list[0].startTime).valueOf());
        startDate = dayjs(list[0].startTime);
      }

      if (supportSeek()) {
        _this.loading.show();
        _this.seek(startDate.format(FORMAT) + 'Z', endDate.format(FORMAT) + 'Z').catch(() => {
          _this.loading.hide();
        });
      } else {
        _this.loading.show();
        _this
          .pause(startDate.format('YYYYMMDDHHmmss'))
          .then(() => {
            _this.resume(startDate.format('YYYYMMDDHHmmss')).catch(() => {
              _this.loading.hide();
            });
          })
          .catch(() => {
            _this.loading.hide();
          });
      }
    }

    function supportSeek() {
      // 云端默认支持seek  本地需要判断能力集
      return (
        _this.urlInfo.recType === 'cloud' ||
        (_this.options.useSeek && (deviceCapacity as any)?.support_seek_playback === '1')
      );
    }

    // --------------- common event ----------------
    _this.event.on('network.videoFragmentFiles', (data: any) => {
      console.log('network.videoFragmentFiles', data);
      const listData = data.data;
      const len = listData.length;
      const availArr = [];
      if (_this.options.isMobile) {
        // 移动端需要  HH:mm:ss 格式的视频
        for (let i = len - 1; i >= 0; i--) {
          availArr.push({
            startTime: format(listData[i].startTime),
            endTime: format(listData[i].endTime)
          });
        }
        timeLine.setRecord(availArr as any);
        list = data.data;
      } else {
        timeLine.setRecord(data.data);
        list = data.data;
      }
    });

    _this.event.on('network.deviceCapacity', (data: any) => {
      console.log(`%cnetwork.deviceCapacity`, 'font-size: 20px; color:red;', data);
      deviceCapacity = data;
    });

    _this.event.on('EVENT_CALLBACK', (data: any) => {
      if (data.data.type === 'appearFirstFrameCallback') {
        if (_timer) {
          clearInterval(_timer);
          _timer = null!;
        }

        let time = 0;

        _timer = window.setInterval(() => {
          if (timeLine) {
            time = (_this.getOSDTime() || 0) * 1000;
            if (time && typeof time === 'number') {
              if (_this.options.isMobile) {
                // h5 的seek 时间格式 HH:mm:ss
                (timeLine as MobileTimeLine).setCurrent(
                  _seekTime > 0 ? format(_seekTime) : format(time)
                );
              } else {
                (timeLine as TimeLine).setCurrent(_seekTime > 0 ? _seekTime : time);
              }
              // seek
              // 拖拽 <- (鼠标方向)
              if (_seekType === 1) {
                if (time >= _seekTime) {
                  _this.loading.hide();
                  _seekTime = 0;
                  _seekType = 0;
                }
                _this.logger.log('drag <- _seekTime: ', _seekTime);
              } else if (_seekType === 2 && supportSeek()) {
                // FIXME: 由于OSD本身就比_seekTime 大， 并且 seek 成功后获取的OSD 可能比 _seekTime 大 也可能比_seekTime 小(由于解码视频返回的OSD不确定), 需要流媒体返回成功标识才可以进行判断
                // FIXME: 暂时定时三秒结束loading
                // 拖拽 -> (鼠标方向)
                const __timer = setTimeout(() => {
                  _this.loading.hide();
                  _seekTime = 0;
                  _seekType = 0;
                  clearTimeout(__timer);
                }, 3000);
                _this.logger.log('drag -> _seekTime: ', _seekTime);
              } else if (_seekType === 2 && !supportSeek()) {
                _seekTime = 0;
                _seekType = 0;
              }

              const endDate = list.length ? dayjs(list[list.length - 1].endTime) : dayjs();
              // seek 回放结束
              if (supportSeek() && time >= endDate.valueOf()) {
                _this.event.emit('EVENT_CALLBACK', {
                  data: {
                    type: 'pluginErrorHandler',
                    code: 6701
                  }
                });
              }
            } else {
              _this.logger.warn('getOSDTime ', time);
            }
          }
        }, 1000);
      } else if (data.data.type === 'setRunTimeInfoCallBack') {
        // 实时信息回调 会多次触发
        if (data?.data?.data?.nErrorCode === 5 && !ended) {
          _seekTime = 0;
          _seekType = 0;
          ended = true;
        }
      }
    });

    _this.event.on('StreamClient.autoClose', () => {
      // wss 出错断流
      console.log('%cStreamClient.autoClose', 'font-size: 20px; color:red;');
      _seekTime = 0;
      _seekType = 0;
      ended = true;
    });

    ['playError', 'pause', 'stop', 'destroy'].forEach((eventType) => {
      _this.event.on(eventType, () => {
        if (_timer) {
          window.clearInterval(_timer);
          _timer = null!;
        }
      });
    });

    _this.event.on('urlChange', () => {
      timeLine.setRecord([]);
      // PC 上一个视频时间片段进行清除
      if (typeof (timeLine as TimeLine).update === 'function') (timeLine as TimeLine).update({});
      list = [];
    });

    if (!_this.options.isMobile) {
      _this.event.on('resize', (data: any) => {
        setTimeout(() => {
          if (data.data?.width > 0) {
            (timeLine as TimeLine).setWidth({ width: data.data?.width - 58 - 40 });
          }
        }, 10);
      });
    }

    return $container;
  };
}

export default TimeLineLoader;
